* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.people {
  grid-template-columns: repeat(2, 200px);
  grid-template-rows: repeat(5, 200px, 231px);
  justify-content: center;
  align-items: center;
  gap: 30px 40px;
  padding: 30px 35px 155px;
  display: grid;
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, 200px, 231px);
    padding: 30px 160px 155px;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    grid-template-rows: repeat(3, 200px, 231px);
    padding: 30px 55px 197px;
  }
}

@media (width >= 1500px) {
  .people {
    padding: 30px 305px 197px;
  }
}

.person {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 231px;
  display: flex;
}

.person__photo {
  margin-bottom: 8px;
  position: relative;
}

.person__img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
}

.person__name {
  color: #2f4f4f;
  margin: 0 auto;
  font-family: Arial;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
}

.person__extra {
  font-size: 18px;
  line-height: 21px;
  display: none;
}

.person:hover {
  background-color: #eee;
  border-radius: 5%;
  width: 220px;
  height: 287px;
  transform: scale(1.2);
}

.person:hover .person__extra {
  text-align: center;
  margin-top: 9px;
  display: block;
}

@media (width >= 1000px) {
  .person:hover {
    transition: transform 1s;
  }
}
